.who-we-are {
  box-sizing: border-box;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #f2f6fa;
  padding: 148px 0;
  max-width: 1600px;

  
  &__heading {
    @include font-poppins(32px, 600, 150%, #253757);
  }

  &__text {
    @include font-open-sans(24px, 600, 150%, #6C788b);
    max-width: 754px;
  }

  @media (max-width: 758px) {
    .who-we-are{
      
      &__wrapper {
        margin: 20px 20px 0;
      }

      &__heading {
        text-align: center;
      }
    }
  }
}